import HttpService from './HttpService';

export default {
    getByTitle
};

const endpoint = '/coupons';

async function getByTitle(title) {
    console.log("title CS", title)
    const coupons = await HttpService.get(`${endpoint}/${title}`);
    let jsObj = coupons.map(element => {
        element.price = decodeURI(element.price)
        element.price = element.price.trim()
        element.price = element.price.replaceAll("&#34;", "\"")
        element.price = element.price.replaceAll("&#10;", "")
        element.price = element.price.replaceAll("&#39;", "\"")
        return element
    });
    return jsObj;
}
